<template>
  <div>
      <v-add ref="add" :info="info"></v-add>
      <v-list @detail="ed($event)"></v-list>
  </div>
</template>

<script>
import vList from "./list"
import vAdd from "./add"
export default {
    data(){
        return {
            info:{
                isShow:false,
                title:"",
            }
        }
    },
    methods:{
        ed(id){
            this.info.isShow=true
            this.info.title="编辑会员"
            this.$refs.add.deta(id)
        }

    },
    components:{
        vList,
        vAdd
    }
}
</script>

<style scoped>
.el-button{
    margin:20px 0;
}
</style>


