<template>
  <div>
         <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="手机号" :label-width="formLabelWidth">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="昵称" :label-width="formLabelWidth" >
          <el-input v-model="form.nickname" autocomplete="off"></el-input>
        </el-form-item>

         <el-form-item label="密码" :label-width="formLabelWidth" >
          <el-input type="password" v-model="form.password" autocomplete="off"></el-input>
          <i>留空则不修改</i>
        </el-form-item>

        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
             v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary"  @click="update">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {mapGetters,mapActions} from "vuex"
import { successAlert, warningAlert } from '../../util/alert'
import {oneMember,memberUpdata} from "../../util/request"
export default {
    props:["info"],
    data(){
        return{
             formLabelWidth:"120px",
             form:{
                 nickname:"",
                 phone:"",
                 password:"",
                 status:1
             },
             userPass:"",
        }
    },
    computed:{
        ...mapGetters({
            listm:"member/list"
        })
    },
    methods:{
        ...mapActions({
            requestMember:"member/memberActions"
        }),
        deta(id){
           oneMember({uid:id}).then(res=>{
               this.form=res.data.list
               this.userPass= res.data.list.password
               this.form.password=''
               this.form.uid=id
           })
        },
        update(){
            if (!this.form.password) {
                this.form.password=this.userPass
            }
            memberUpdata(this.form).then(res=>{
                if(res.data.code==200){
                    successAlert(res.data.msg)
                    this.requestMember()
                    this.cancel()
                }else{
                    warningAlert(res.data.msg)
                }
            })
        },
        cancel(){
            this.info.isShow=false
            this.form={
                nickname:"",
                 phone:"",
                 password:"",
                 status:1
            }
        }
    }
}
</script>

<style>

</style>


